import React, { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import '../styles/DetectionPage.css';
import { Button, message, Radio } from 'antd';
import 'antd/dist/reset.css';
import { SERVER_TEXT } from '../api/Api';
import axios from 'axios';

const TextDetectionPage = () => {
  const navigate = useNavigate();
  const [inputType, setInputType] = useState('text'); // 'text' or 'file'
  const [inputText, setInputText] = useState('');
  const [selectedFile, setSelectedFile] = useState(null);
  const [isProcessing, setIsProcessing] = useState(false);
  const [results, setResults] = useState(null);
  const fileInputRef = useRef(null);

  // 
  const handleInputTypeChange = (e) => {
    const type = e.target.value;
    setInputType(type);
    setInputText('');
    setSelectedFile(null);
    setResults(null);
    if (fileInputRef.current) fileInputRef.current.value = '';
  };

  // 
  const handleTextChange = (event) => {
    setInputText(event.target.value);
    setResults(null);
  };

  // 
  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      setSelectedFile(file);
      setResults(null);
      //  txt 
      if (file.type === 'text/plain') {
        const reader = new FileReader();
        reader.onload = (e) => {
          setInputText(e.target.result);
        };
        reader.readAsText(file);
      } else {
        setInputText('');
      }
    }
  };

  // 
  const handleProcess = async () => {
    let text = inputText;
    if (inputType === 'file' && selectedFile && !inputText) {
      message.warning('txttxt');
      return;
    }
    if (!text) {
      message.warning('');
      return;
    }
    setIsProcessing(true);
    setResults(null);
    try {
      const response = await axios.post(SERVER_TEXT, { text });
      setResults(response.data);
    } catch (err) {
      message.error('');
    } finally {
      setIsProcessing(false);
    }
  };

  // 
  const handleReupload = () => {
    setSelectedFile(null);
    setInputText('');
    setResults(null);
    if (fileInputRef.current) fileInputRef.current.value = '';
  };

  return (
    <center>
    <div className="detection-container">
      <h1></h1>
      {/* Radio */}
      <div style={{ display: 'flex', justifyContent: 'center', marginBottom: 24 }}>
        <Radio.Group
          value={inputType}
          onChange={handleInputTypeChange}
          buttonStyle="solid"
        >
          <Radio.Button value="text"></Radio.Button>
          <Radio.Button value="file"></Radio.Button>
        </Radio.Group>
      </div>

      {/*  */}
      {inputType === 'text' && (
        <div style={{ width: 400, margin: '0 auto', marginBottom: 24 }}>
          <textarea
            value={inputText}
            onChange={handleTextChange}
            placeholder="..."
            rows={8}
            className="text-area"
            style={{ width: '100%' }}
          />
        </div>
      )}

      {/*  */}
      {inputType === 'file' && (
        <div style={{ width: 400, margin: '0 auto', marginBottom: 24 }}>
          {!selectedFile ? (
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: 12 }}>
              <input
                type="file"
                accept=".txt"
                onChange={handleFileChange}
                ref={fileInputRef}
                style={{ display: 'none' }}
              />
              <Button onClick={() => fileInputRef.current.click()} style={{ width: 160 }}>
                
              </Button>
              <div style={{ color: '#888', fontSize: 13 }}>txt</div>
            </div>
          ) : (
            <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: 12 }}>
              <div>
                <span style={{ fontWeight: 500 }}></span>
                <span>{selectedFile.name}</span>
              </div>
              {selectedFile.type === 'text/plain' && (
                <textarea
                  value={inputText}
                  onChange={handleTextChange}
                  rows={8}
                  className="text-area"
                  style={{ width: '100%' }}
                />
              )}
              <Button onClick={handleReupload} disabled={isProcessing}>
                
              </Button>
            </div>
          )}
        </div>
      )}

      {/*  */}
      <div style={{ textAlign: 'center', marginBottom: 24 }}>
        <Button
          type="primary"
          onClick={handleProcess}
          loading={isProcessing}
          disabled={isProcessing || (!inputText && !selectedFile)}
          style={{ width: 160 }}
        >
          {isProcessing ? '...' : ''}
        </Button>
      </div>

      {/*  */}
      {isProcessing && (
        <div className="processing-indicator">
          <p>...</p>
          <div className="spinner"></div>
        </div>
      )}

      {/*  */}
      {results && (
        <div
          className="results-section"
        >
          <h2></h2>
            <div className="summary-box"
            style={{
            background:results['violation_categories'][0] === ''
              ? ' #e6ffed'
              : ' #fff1f0',
            border: '1px solid',
            borderColor:results['violation_categories'][0] === ''
              ? ' #52c41a'
              : ' #ff4d4f'
              }}
            >
            <h3></h3>
            <p>
              {results['violation_categories']?.join('')}
            </p>
          </div>
          {/*  */}
          {!(results['violation_categories']?.length === 1 && results['violation_categories'][0] === '') && (
            <div className="summary-box"
            style={{
            background:results['violation_categories'][0] === ''
              ? ' #e6ffed'
              : ' #fff1f0',
            border: '1px solid',
            borderColor:results['violation_categories'][0] === ''
              ? ' #52c41a'
              : ' #ff4d4f'
          }}  
            >
              <h3></h3>
              <p>
                {results['violation_texts']?.join('')}
              </p>
            </div>
          )}
        </div>
      )}
      </div>
      </center>
  );
};

export default TextDetectionPage;