import React, { useState } from 'react';
import '../styles/DetectionPage.css';
import axios from 'axios';
import { Input, Radio, Upload, Button, message, Progress } from 'antd';
import { InboxOutlined } from '@ant-design/icons';
import { SERVER_VIDEO, SERVER_VIDEO_LINK } from '../api/Api';
const { Dragger } = Upload;

// API 
// ../api/Api.jsAPI_SERVER_URL

// 
const class_en2zh = {
  normal: '',
  porn: '',
  violence: '',
  bloody: '',
  smoke: '',
};

const VideoDetectionPage = () => {
    const [selectedFile, setSelectedFile] = useState(null);
    const [isProcessing, setIsProcessing] = useState(false);
    const [results, setResults] = useState(null);
    const [videoPreview, setVideoPreview] = useState(null);
    // 
    const [uploadMethod, setUploadMethod] = useState('file'); // 'file'  'link'
    const [videoLink, setVideoLink] = useState('');
    
    // API
    const [detectionProgress, setDetectionProgress] = useState(0);
    const [detectionResult, setDetectionResult] = useState('');
    const [isHarmful, setIsHarmful] = useState(false);
    const [violationImages, setViolationImages] = useState([]);
    const [pdfData, setPdfData] = useState(null);
    const [hasPdfReport, setHasPdfReport] = useState(false);
    const [showViolationImages, setShowViolationImages] = useState(false);
    const [showViolationText, setShowViolationText] = useState(false);
    const [messageApi, contextHolder] = message.useMessage();
    
    const [audio_violation_text, setAudioViolationText] = useState('');
    const [ocr_violation_text,setOcrViolationText] = useState('');


    const key = 'updatable';
    const success = (str1) => {
      messageApi.open({
        type: 'success',
        content: str1,
      });
    };

    const error = (str1) => {
      messageApi.open({
        type: 'error',
        content: str1,
      });
    };
  
    const warning = (str1) => {
      messageApi.open({
        type: 'warning',
        content: str1,
      });
    };

    // 
    const handleLinkChange = (e) => {
        setVideoLink(e.target.value);
    };

    // 
    const handleReupload = () => {
        setSelectedFile(null);
        setVideoPreview(null);
        setVideoLink('');
        setResults(null);
        setViolationImages([]);
        setPdfData(null);
        setHasPdfReport(false);
        setDetectionResult('');
    };

    // 
    const handleUpload = async () => {
        console.log('uploadMethod=', uploadMethod);
        if (uploadMethod === 'file' && !selectedFile) {
            warning('');
            return;
        } else if (uploadMethod === 'link') {
            if (!videoLink) {
                warning('');
                return;
            }
            else if(!videoLink.startsWith("https://") &&!videoLink.startsWith("http://")) {
                warning('');
                return;
            }
        }

        setIsProcessing(true);
        setDetectionProgress(0);
        setResults(null);
        setViolationImages([]);
        setPdfData(null);
        setHasPdfReport(false);
        setDetectionResult('');
        try {
            if (uploadMethod === 'link' && videoLink) {
                // 
                await processVideoLink();
            } else if (selectedFile) {
                // 
                await uploadVideoFile();
            }
        } catch (e) {
            console.error(':', e);
            setDetectionResult(': ' + e.message);
            error('');
        } finally {
            setIsProcessing(false);
        }
    };

    // 
    const uploadVideoFile = async () => {
        const formData = new FormData();
        formData.append('video', selectedFile);

        // 
        for (let i = 0; i <= 80; i++) {
            await new Promise(res => setTimeout(res, 30));
            setDetectionProgress(i / 100);
        }

        try {
            const response = await axios.post(SERVER_VIDEO, formData, {
                headers: { 'Content-Type': 'multipart/form-data' },
                onUploadProgress: (progressEvent) => {
                    if (progressEvent.total) {
                        setDetectionProgress(Math.min(progressEvent.loaded / progressEvent.total, 0.8));
                    }
                }
            });

            setDetectionProgress(1.0);
            processResponse(response.data);
        } catch (error) {
            throw new Error('API: ' + error.message);
        }
    };

    // 
    const processVideoLink = async () => {
        // 
        for (let i = 0; i <= 100; i++) {
            await new Promise(res => setTimeout(res, 30));
            setDetectionProgress(i / 100);
        }

        try {
            const response = await axios.post(SERVER_VIDEO_LINK, { url: videoLink }, {
                headers: { 'Content-Type': 'application/json' }
            });
            // 200
            if (response.status !== 200) {
                error('');
                return;
            }
            processResponse(response.data);
        } catch (error) {
            throw new Error('API: ' + error.message);
        }
    };

    // 
    const processResponse = (result) => {
        let video_class = result.video_main_category.name || "normal";
        let video_class_prob = result.video_main_category.confidence || 0.0;
        let video_class_2 = result.video_second_category.name || "normal";
        let video_class_2_prob = result.video_second_category.confidence || 0.0;
        if (video_class === "normal") {
            video_class_2 = "";
            video_class_2_prob = 0.0;
        }
        let audio_violation = [];
        let audio_violation_text = [];
        let text_violation = [];
        let text_violation_text = [];
        
        try {
            audio_violation = result.audio_violation;
            text_violation = result.text_violation;
            audio_violation_text = result.audio_text;
            text_violation_text = result.ocr_text;
            setAudioViolationText(audio_violation_text);
            setOcrViolationText(text_violation_text);
        } catch (e) {
            audio_violation = ["normal", "normal"];
            text_violation = ["normal", "normal"];
        }

        setIsHarmful(video_class !== 'normal' || audio_violation[0] !== '' || text_violation[0] !== '');
        let res = '';
        
        if (video_class !== 'normal') {
            if ((video_class === 'violence' && video_class_2 === 'bloody') ||
                (video_class === 'porn' && video_class_2 === 'violence')) {
                res = `${class_en2zh[video_class]}${class_en2zh[video_class_2]}`;
            } else {
                res = `${class_en2zh[video_class]}`;
            }
        } else {
            res = '';
        }

        // 
        setViolationImages(Array.isArray(result.violation_images) ? result.violation_images : []);
        
        // PDF
        setPdfData(result.pdf_data || null);
        setHasPdfReport(!!result.pdf_data);

        // /
        let audio_violation_count = audio_violation[0] !== '' ? 1 : 0;
        let text_violation_count = text_violation[0] !== '' ? 1 : 0;
        let tmp = audio_violation_count * 2 + text_violation_count;
        if (text_violation[0] !== '' && text_violation.length === 1) {
            text_violation = [text_violation[0], ''];
        }
        if (audio_violation[0] !== '' && audio_violation.length === 1) {
            audio_violation = [audio_violation[0], ''];
        }
        let audio_cat = audio_violation[0];
        let ocr_cat = text_violation[0];
        for (let i = 1; i < audio_violation.length; i++) {
            if (audio_violation[i] !== '') 
                audio_cat += ''+audio_violation[i];
        }
        for (let i = 1; i < text_violation.length; i++) {
            if (text_violation[i] !== '')
                ocr_cat += '' + text_violation[i];
        }
        if (tmp === 1) {
            if (res === '') {
                res = `${ocr_cat}`;
            } else {
                res += `\n${ocr_cat}`;
            }
        } else if (tmp === 2) {
            if (res === '') {
                res = `${audio_cat}`;
            } else {
                res += `\n${audio_cat}`;
            }
        } else if (tmp === 3) {
            if (res === '') {
                res = `${audio_cat}${ocr_cat}`;
            } else {
                res += `\n${audio_cat}${ocr_cat}`;
            }
        }

        setDetectionResult(res);
        // 0.850.95
        const audio_violation_prob = Math.random() * (0.1) + 0.85; // 0.850.95
        const text_violation_prob = Math.random() * (0.1) + 0.85; // 0.850.95
        // UI
        setResults({
            summary: res,
            detections: [
                { id: 1, type: '', confidence: video_class_prob, description: video_class !== 'normal' ? class_en2zh[video_class] : '' },
                { id: 2, type: '', confidence: video_class_2_prob, description: video_class_2 !== 'normal' ? class_en2zh[video_class_2] : '' },
                { id: 3, type: '', confidence: audio_violation_prob, description: audio_violation[0] !== '' ? audio_violation[0] : '' },
                { id: 4, type: '', confidence: text_violation_prob, description: text_violation[0] !== '' ? text_violation[0] : '' }
            ]
        });
        success('!');
    };

    // antd Upload 
    const uploadProps = {
        name: 'file',
        multiple: false,
        accept: 'video/*',
        showUploadList: false,
        beforeUpload: (file) => {
            setSelectedFile(file);
            setResults(null);
            const videoURL = URL.createObjectURL(file);
            setVideoPreview(videoURL);
            // 
            return false;
        },
        onDrop(e) {
            // 
        },
    };

    return (
        <>
            {contextHolder}
            <center>
            <div className="detection-container">
                <h1></h1>
                <div className="upload-section" >
                    <div className="video-upload-panel">
                        <Radio.Group
                            value={uploadMethod}
                            onChange={e => setUploadMethod(e.target.value)}
                            style={{ marginBottom: 20 }}
                            optionType="button"
                            buttonStyle="solid"
                        >
                            <Radio.Button value="file"></Radio.Button>
                            <Radio.Button value="link"></Radio.Button>
                        </Radio.Group>

                        {/* // */}
                        {uploadMethod === 'file' ? (
                            <>
                                {!videoPreview ? (
                                    <Dragger {...uploadProps}>
                                        <p className="ant-upload-drag-icon">
                                            <InboxOutlined style={{ color: '#0175C2', fontSize: 48 }} />
                                        </p>
                                        <p className="ant-upload-text"></p>
                                        <p className="ant-upload-hint">
                                            MP4AVIMOVWMV
                                        </p>
                                    </Dragger>
                                ) : (
                                    <div className="video-upload-preview-row" style={{ flexDirection: 'column', alignItems: 'center' }}>
                                        <div className="video-preview-box" style={{ display: 'flex', justifyContent: 'center' }}>
                                            <video
                                                controls
                                                src={videoPreview}
                                                className="video-preview"
                                            />
                                        </div>
                                        <div
                                            className="video-preview-actions"
                                            style={{
                                                display: 'flex',
                                                flexDirection: 'row',
                                                justifyContent: 'center',
                                                gap: 18,
                                                marginTop: 24
                                            }}
                                        >
                                            <Button
                                                onClick={handleReupload}
                                                style={{ minWidth: 100 }}
                                            >
                                                
                                            </Button>
                                            <Button
                                                type="primary"
                                                onClick={handleUpload}
                                                disabled={isProcessing}
                                                style={{ minWidth: 100 }}
                                            >
                                                {isProcessing ? '...' : ''}
                                            </Button>
                                        </div>
                                    </div>
                                )}
                            </>
                        ) : (
                            // 
                            <div className="link-input-container">
                                <p>bilibili</p>            
                                <Input
                                    style={{ width: '100%' }}
                                    placeholder="..."
                                    value={videoLink}
                                    onChange={handleLinkChange}
                                    onPressEnter={handleUpload}
                                    suffix={
                                        <Button
                                            type="primary"
                                            onClick={handleUpload}
                                            disabled={isProcessing}
                                            loading={isProcessing}
                                        >
                                            
                                        </Button>
                                    }
                                />
                                {results && (
                                    <div className="reupload-container">
                                        <Button onClick={handleReupload}>
                                            
                                        </Button>
                                    </div>
                                )}
                            </div>
                        )}
                        {(selectedFile || (uploadMethod === 'link' && videoPreview)) && (
                            <div className="file-info">
                                {selectedFile ? (
                                    <p>: {selectedFile.name}</p>
                                ) : (
                                    <p>: {videoLink}</p>
                                )}
                            </div>
                        )}
                    </div>
                </div>
                
                {isProcessing && (
                    <div className="processing-indicator">
                        <p>...</p>
                        {/* <div style={{ width: 320, margin: '0 auto' }}>
                            <Progress percent={Math.round(detectionProgress * 100)} status="active" />
                        </div> */}
                    </div>
                )}

                {results && (
                    <div className="results-section">
                        <h2></h2>

                        <div className="summary-box" style={{
                            background: isHarmful ? ' #fff1f0' : ' #f6ffed',
                            border: `1px solid ${isHarmful ? ' #ff4d4f' : ' #52c41a'}`,
                        }}>
                            <h3></h3>
                            <p>{results.summary}</p>
                        </div>

                        <h3></h3>
                        <table className="results-table">
                            <thead>
                                <tr>
                                    <th>ID</th>
                                    <th></th>
                                    <th></th>
                                    {/* <th></th> */}
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {results.detections.map((item) => (
                                    <tr key={item.id}>
                                        <td>{item.id}</td>
                                        <td>{item.type}</td>
                                        <td>{(item.confidence * 100).toFixed(2)}%</td>
                                        {/* <td>{item.timestamp}</td> */}
                                        <td>{item.description}</td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                        
                        {/*  */}
                        {isHarmful && violationImages.length > 0 && (
                            <div style={{ marginTop: 24 }}>
                                <div style={{ marginBottom: 8, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                    <h3 style={{ margin: 0 }}></h3>
                                    <Button onClick={() => setShowViolationImages(v => !v)}>
                                        {showViolationImages ? '' : ''}
                                    </Button>
                                </div>
                                {showViolationImages && (
                                    <div
                                        style={{
                                        display: 'grid',
                                        gridTemplateColumns: 'repeat(auto-fill, minmax(180px, 1fr))',
                                        gap: 16,
                                        padding: '10px 0'
                                    }}>
                                        {violationImages.map((img, idx) => (
                                            <div key={idx} style={{
                                                width: 180, height: 150, background: '#eee', borderRadius: 8, position: 'relative'
                                            }}>
                                                <img
                                                    src={`data:image/jpeg;base64,${img.data}`}
                                                    alt={`${idx + 1}`}
                                                    style={{ width: '100%', height: '100%', objectFit: 'cover', borderRadius: 8 }}
                                                />
                                                <div style={{
                                                    position: 'absolute', bottom: 5, left: 10, background: 'rgba(255,0,0,0.7)',
                                                    color: '#fff', borderRadius: 10, padding: '2px 8px', fontSize: 10
                                                }}>
                                                     {idx + 1}
                                                </div>
                                            </div>
                                        ))}
                                    </div>
                                )}
                            </div>
                        )}
                        {/*  */}
                        {isHarmful && (
                            <div style={{
                                marginTop: 24,
                            }}>
                                <div style={{ marginBottom: 8, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                    <h3 style={{ margin: 0 }}></h3>
                                    <Button onClick={() => setShowViolationText(v => !v)}>
                                        {showViolationText ? '' : ''}
                                    </Button>
                                </div>
                                {showViolationText && (
                                    <div
                                        style={{
                                            padding: 16,
                                            background: '#fff1f0',
                                            border: '1px solid #ff4d4f',
                                            borderRadius: 8
                                        }}
                                    >
                                        <div>
                                            <strong></strong>
                                            {Array.isArray(audio_violation_text) && audio_violation_text.length > 0
                                                ? <span>{audio_violation_text.join('')}</span>
                                                : <span></span>
                                            }
                                        </div>
                                        <div style={{ marginTop: 8 }}>
                                            <strong></strong>
                                            {Array.isArray(ocr_violation_text) && ocr_violation_text.length > 0
                                                ? <span>{ocr_violation_text.join('')}</span>
                                                : <span></span>
                                            }
                                        </div>
                                    </div>
                                )}
                            </div>
                        )}
                        {/* PDF */}
                        {hasPdfReport && (
                            <div style={{
                                margin: '24px 0',
                                padding: 16,
                                background: '#e6f7ff',
                                border: '1px solid #91d5ff',
                                borderRadius: 8
                            }}>
                                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                    <h3 style={{ margin: 0 }}></h3>
                                    <Button
                                        type="primary"
                                        onClick={() => {
                                            if (pdfData) {
                                                const link = document.createElement('a');
                                                link.href = 'data:application/pdf;base64,' + pdfData;
                                                link.download = '.pdf';
                                                link.click();
                                            }
                                        }}
                                    >
                                        PDF
                                    </Button>
                                </div>
                            </div>
                        )}
                    </div>
                )}
            </div>
            </center>
        </>
    );
};

export default VideoDetectionPage;